import Viewer from "@phuocng/react-pdf-viewer";
import pdfdoc from "../../img/FULL_CV_AMIR.pdf";
// Import the CSS
import "@phuocng/react-pdf-viewer/cjs/react-pdf-viewer.css";
import { translate, Trans } from "react-i18next";


const CV = (props) => (
   
      
      <div>
        <div class="centt">
   
        <i class="far fa-file"></i> &nbsp;<b>{props.t("menu.resume")}</b><hr></hr>
        </div>
        <Worker workerUrl="https://unpkg.com/pdfjs-dist@2.3.200/build/pdf.worker.min.js">
          <Viewer fileUrl={pdfdoc} />
        </Worker>
      </div>
   
  );
  
  export default translate("common")(CV);


