
import React, { useState } from "react";
import { translate, Trans } from "react-i18next";
import {
    MDBBtn,
    MDBRow,
    MDBBadge,
    MDBCard,
    MDBCardBody,
    MDBCardImage,
    MDBCardTitle,
    MDBCardText,
    MDBCol,MDBIcon, MDBInput 
  } from "mdbreact";
  
  import {
    MDBContainer,
    MDBModal,
    MDBModalBody,
    MDBModalHeader,
    MDBModalFooter,
  } from "mdbreact";

  import prof from "../img/prof.png";
  import qr from "../img/qr.png";



 export class ProfileInfo extends React.Component {

  constructor(props) {
    super(props);
  
   
    };
 
    state = {
      wechat: false,
    };
  
    toggler = () => {
      this.setState({
        wechat: !this.state.wechat,
      });
    };
  
    render() {
      
      return (
        <React.Fragment>
          <MDBCol>
            <MDBCard className="cards" style={{ width: "100%" }}>
              <div class="col-md-12">
                <div class="pull-left">
                  {" "}
                  <img
                    src={prof}
                    width="100px"
                    className="profilepic img-fluid"
                  ></img>
                </div>
                <div class="pull-left infonew">
                  <MDBCardBody>
                    <div className="sideprof">
                      <br></br>
                      <MDBCardTitle> {this.props.t("profile.name")}</MDBCardTitle>
                      
                      <MDBCardText className="aboutme">
                      {this.props.t("profile.position")}<br></br><br></br><br></br>
    
                  
  
                       
                        <hr></hr>
                      </MDBCardText>
                      <div className="profile_icons">
                        <a href="https://github.com/Amirq17/" target="_blank">
                          <i className="fab fa-github iconka"></i>
                        </a>
                        <a
                          href="https://cn.linkedin.com/in/amiribraimov?trk=profile-badge"
                          target="_blank"
                        >
                          <i className="fab fa-linkedin iconka"></i>
                        </a>
                        <a
                          href="https://api.whatsapp.com/send?phone=77776341393"
                          target="_blank"
                        >
                          <i class="fab fa-whatsapp-square iconka"></i>
                        </a>
                        <a href="#" onClick={this.toggler}>
                          <i class="fab fa-weixin iconka"></i>
                        </a>
  
                        <a
                          href="mailto:ibraimov.amir1996@gmail.com"
                          target="_blank"
                        >
                          <i class="fas fa-envelope iconka"></i>
                        </a>
                      </div>
                      <hr></hr>
                    </div>
                  </MDBCardBody>
                </div>
              </div>
            </MDBCard>
          </MDBCol>
  
          <MDBContainer>
            <MDBModal
              isOpen={this.state.wechat}
              togglr={this.toggler}
              className="centt"
            >
              <MDBModalHeader toggle={this.toggler}>Wechat</MDBModalHeader>
              <MDBModalBody>
                <img src={qr} className="img-fluid"></img>
              </MDBModalBody>
            </MDBModal>
          </MDBContainer>
  
          <MDBCol>
            <MDBCard className="cards">
              <MDBCardBody>
                <MDBCardTitle className="center">
                  <i className="fas fa-language"></i>
                  <p className="center"> {this.props.t("profile.languages")}</p>
                </MDBCardTitle>
                <hr></hr>
  
                <div className="progress">
                  <div
                    className="progress-bar bg-info"
                    role="progressbar"
                    aria-valuenow=""
                    aria-valuemin="0"
                    aria-valuemax="100"
                    style={{ width: "100%" }}
                  >
                   {this.props.t("languages.Russian")}
                  </div>
                </div>
                <br></br>
                <div className="progress">
                  <div
                    className="progress-bar bg-info"
                    role="progressbar"
                    aria-valuenow=""
                    aria-valuemin="0"
                    aria-valuemax="100"
                    style={{ width: "100%" }}
                  >
                  {this.props.t("languages.English")}
                  </div>
                </div>
                <br></br>
                <div className="progress">
                  <div
                    className="progress-bar bg-info"
                    role="progressbar"
                    aria-valuenow=""
                    aria-valuemin="0"
                    aria-valuemax="100"
                    style={{ width: "100%" }}
                  >
                    {this.props.t("languages.Japanese")}
                  </div>
                </div>
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
  
          <MDBCol>
            <MDBCard className="cards fixxx">
              <MDBCardBody>
                <MDBCardTitle className="center">
                  <i className="fas fa-laptop-code"></i>
                  <p className="center"> {this.props.t("profile.skills")}</p>
                </MDBCardTitle>
                <hr></hr>
                <div className="centt">
               
                
                  <MDBBadge color="primary">HTML</MDBBadge>&nbsp;
                  <MDBBadge color="primary">CSS</MDBBadge>&nbsp;
                  <MDBBadge color="primary">Sass</MDBBadge>&nbsp;
                  <MDBBadge color="primary">JavaScript</MDBBadge>&nbsp;
                  <MDBBadge color="primary">SPA</MDBBadge>&nbsp;
                  <MDBBadge color="primary">React.js</MDBBadge>&nbsp;
                  <MDBBadge color="primary">Bootstrap</MDBBadge>&nbsp;
                  <MDBBadge color="primary">jQuery</MDBBadge>&nbsp;
                  <MDBBadge color="primary">Tailwind</MDBBadge>&nbsp;
                  <MDBBadge color="primary">Webpack</MDBBadge>&nbsp;
                  <MDBBadge color="primary">Java J2EE</MDBBadge>&nbsp;
                  <MDBBadge color="primary">Cypress JavaScript Automated Testing</MDBBadge>&nbsp;
                  <MDBBadge color="primary">MySql</MDBBadge>&nbsp;
                  <MDBBadge color="primary">Web Design</MDBBadge>&nbsp;
                 
                </div>
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
        </React.Fragment>
      );
    }
  }
  export default translate("common")(ProfileInfo);