import prof from "../img/prof.png";
import movie from "../img/movie.mp4";
import sorbent from "../img/sorbent.png";
import pizza from "../img/pizza.png";
import cloer from "../img/cloer.png";
import hospital from "../img/hospital.png";
import tizim from "../img/tizim.png";
import gamestore from "../img/gamestore.png";
import port from "../img/port.gif";
import qr from "../img/qr.png";
