import Home from "../Components/Routes/About";
import Portfolioexamples from "../Components/Routes/Portfolio";
import Contact from "./Routes/Contact";
import ProfileInfo from "../Components/ProfileInfo";
import {
  MDBBtn,
  MDBRow,
  MDBBadge,
  MDBCard,
  MDBCardBody,
  MDBCardImage,
  MDBCardTitle,
  MDBCardText,
  MDBCol,MDBIcon, MDBInput 
} from "mdbreact";


import CV from '../Components/Routes/Resume';
import {
  MDBContainer,
  MDBModal,
  MDBModalBody,
  MDBModalHeader,
  MDBModalFooter,
} from "mdbreact";
import { CardText } from "react-bootstrap/Card";

import { translate, Trans } from "react-i18next";
import { HashRouter as Router, Switch, Route, Link } from "react-router-dom";






const NavigationRouter = (props, { name }) => (
      <React.Fragment>
        <MDBCol>
          <MDBCard className="cards" style={{ width: "100%" }}>
            <MDBCardBody>
              <div className="centt">
                <Router>
                 
                    <Link className="btn" to="/">
                    <i class="far fa-address-card"></i>  &nbsp;{props.t("menu.home")}   
                    </Link>

                    <Link className="btn" to="/CV">
                    <i class="far fa-file"></i> &nbsp; {props.t("menu.resume")}   
                    </Link>

                    <Link className="btn" to="/portfolioexamples">
                    <i class="far fa-folder-open"></i> &nbsp; {props.t("menu.portfolio")}   
                    </Link>

                    <Link className="btn" to="/Contact">
                    <i class="far fa-envelope"></i>&nbsp;   {props.t("menu.contact")}   
                    </Link>

                    <hr />
                    <Switch>
                      <Route exact path="/">
                        <Home />
                      </Route>

                      <Route path="/portfolioexamples">
                        <Portfolioexamples />
                      </Route>
                      <Route exact path="/CV">
                        <CV />
                      </Route>
                      <Route exact path="/contact">
                        <Contact />
                      </Route>
                    </Switch>
                  
                </Router>
                

                <hr></hr>
                <br></br>
              </div>
            </MDBCardBody>
          </MDBCard>
        </MDBCol>

        
      </React.Fragment>
    );







    export default translate("common")(NavigationRouter);
